body.darkTheme {
  background-color: $background-dark-color;
  color: $background-color;

  .sun {
    fill: $primary-blue-color;
  }

  .moon {
    fill: $primary-grey-color;
  }

  .header {
    border-bottom: 1px solid rgba(244, 244, 244, 0.2);
    background-color: $background-dark-color;
  }

  .header-menu-container.is-open {
    background-color: $background-dark-color;
  }

  #nav-icon span {
    background: $background-color;
    border-color: $background-color;
  }

  .header-logo-text {
    color: $background-color;
  }

  .header-search-input {
    border: 1px solid $background-color;
    color: $background-color;
  }

  .header-search-input::placeholder {
    color: $background-color;
  }

  .header-icon-search {
    fill: $background-color;
  }

  .description__filter__calendar {
    color: $background-color;
  }

  .theme__light {
    color: $background-color;
    opacity: 0.4;
  }

  .theme__dark {
    color: $primary-blue-color;
  }

  .withoutnews-title {
    color: $background-color;
  }

  .card-news-title {
    color: $background-color;
  }

  .card-news-description {
    color: $background-color;
  }

  .category__list__btn:active,
  .category__list__btn:focus,
  .category__list__btn:hover {
    background-color: $primary-blue-color;
    color: $primary-white-color;
  }

  .category__active-btn {
    background-color: $primary-blue-color;
    color: $primary-white-color;
  }

  .calendar {
    background-color: $background-dark-color;
    color: $background-color;
    border: 1px solid #f4f4f4;
  }

  .calendar__arrowSvg {
    fill: $primary-white-color;
  }

  .theme__dark {
    color: $primary-blue-color;
  }

  .calendar {
    background-color: $background-dark-color;
    color: $background-color;
    border: 1px solid #f4f4f4;
  }
  .calendar__arrowSvg {
    fill: $primary-white-color;
  }
}

.theme__dark {
  color: $primary-blue-color;
}

.sun {
  fill: none;
  stroke: $primary-grey-color;
}

.moon {
  fill: none;
  stroke: $primary-blue-color;
}

.withoutnews-title {
  color: $background-color;
}

.card-news-title {
  color: $background-color;
}

.card-news-description {
  color: $background-color;
}
