#nav-icon span {
  height: 0;
  width: 22px;
  opacity: 1;
  background: #111321;
  border: 2px solid #111321;
  border-radius: 2px;
  transition: all .25s ease-in-out;
  display: block;
  position: absolute;
  left: 1px;
  transform: rotate(0);
}

#nav-icon span:nth-child(1) {
  top: 3px;
}

#nav-icon span:nth-child(2), #nav-icon span:nth-child(3) {
  top: 10px;
}

#nav-icon span:nth-child(4) {
  top: 17px;
}

.header-menu-container.is-open ~ #nav-icon span:nth-child(1), .header-menu-container.is-open ~ #nav-icon span:nth-child(4) {
  opacity: 0;
}

.header-menu-container.is-open ~ #nav-icon span:nth-child(2) {
  transform: rotate(45deg);
}

.header-menu-container.is-open ~ #nav-icon span:nth-child(3) {
  transform: rotate(-45deg);
}

